<template>
    <div id="hero_showcase_box">
    </div>
    <div>
        <!-- <div>
            <label for="sort-by">选择排序方式：</label>
            <select
                v-model="sortBy"
                id="sort-by"
            >
                <option value="name">按名称</option>
                <option value="price">按价格</option>
                <option value="date">按日期</option>
            </select>
        </div> -->
        <form action="">
            <input
                type="text"
                name="keyword"
                placeholder="输入关键词"
            >
            <input
                type="submit"
                value="搜索"
            >
        </form>
    </div>
    <div id="box_head">
        <img src="../assets/img/public_img/hero_crest_icon.png">
        <div>
            <h1>英雄</h1>
        </div>
    </div>
    <div id="hero_list">
        <router-link
            v-for="(hero,index) in heroes"
            :key="index"
            :to="`/HeroInfo/${hero.formerly}`"
        >
            <div
                class="hero_card"
                :style="{
                  'background-image': 'url(' + require(`../assets/img/hero_posters/${hero.posters}`) + ')',
                  'background-position': `${hero.x*1.0/hero.width*100}% ${hero.y*1.0/hero.height*100}%`,
                  'background-size': 'cover',
                  }"
            >
                <div>
                    <div>
                        <h2>{{ hero.translation }}</h2>
                        <h1>{{ region.find(item => item.formerly === hero.relevantRegion).translation }}</h1>
                    </div>
                    <div>
                        <p>探索<span>⇢</span></p>
                    </div>
                </div>
            </div>
        </router-link>
    </div>

</template>
<script>
import axios from "axios";
import { reactive, toRefs } from "vue";

export default {
    name: "hero_showcase",
    components: {},
    setup() {
        const data = reactive({
            heroes: [],
            region: [],
            races: [],
            roles: [],
        });
        return {
            ...toRefs(data),
        };
    },
    //监听属性 类似于data概念
    computed: {},
    //监控data中的数据变化
    watch: {},
    //方法集合
    methods: {
        fetchRegion() {
            axios
                .get("http://localhost:8089/api/region")
                .then((response) => {
                    this.region = response.data;
                })
                .catch((error) => {
                    console.error(error);
                });
        },
        fetchHeroes() {
            axios
                .get("http://localhost:8089/api/heroes")
                .then((response) => {
                    this.heroes = response.data;
                })
                .catch((error) => {
                    console.error(error);
                });
        },
        fetchRaces() {
            axios
                .get("http://localhost:8089/api/races")
                .then((response) => {
                    this.races = response.data;
                })
                .catch((error) => {
                    console.error(error);
                });
        },
        fetchRoles() {
            axios
                .get("http://localhost:8089/api/roles")
                .then((response) => {
                    this.roles = response.data;
                })
                .catch((error) => {
                    console.error(error);
                });
        },
    },
    //生命周期 - 创建完成（可以访问当前this实例）
    created() {},
    //生命周期 - 挂载完成（可以访问DOM元素）
    mounted() {
        this.fetchHeroes();
        this.fetchRegion();
        this.fetchRaces();
        this.fetchRoles();
    },
    beforeCreate() {}, //生命周期 - 创建之前
    beforeMount() {}, //生命周期 - 挂载之前
    beforeUpdate() {}, //生命周期 - 更新之前
    updated() {}, //生命周期 - 更新之后
    beforeDestroy() {}, //生命周期 - 销毁之前
    destroyed() {}, //生命周期 - 销毁完成
    activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style lang='less' scoped>
@import url(../assets/css/Clear_style.css);
@import url(../assets/css/hero_showcase.css);
</style>